	module.exports = {"data":{"markdownRemark":{"frontmatter":{"title":"GERBARIJ","type":"lyrics"},"html":"<h1>GERBARIJ</h1>\n<h2>je´l’'</h2>\n<section>\n<p>My s toboj v holodnyh podzemel'jah<br>\ncelovalis' v saharnyj ogon'!<br>\nVynesli nas hrupkie oleni<br>\nna rogah, pohozhih na ladon'.</p>\n<p>Rechka s razvetvljonnymi hvostami<br>\nzadevala putnikov, shipja.<br>\nRyby v nej tonuvshimi svistkami<br>\npropuskali vremja skvoz' sebja.</p>\n<p>Lez na les zeljonoj tkan'ju parus,<br>\nmyshcami derev'ja naprjaglis' — <br>\ni spolzla truhoju ryhloj starost',<br>\nogoljaja mezh stvolami zhizn'!</p>\n<p>Tak slozhilis' kosti lesa v sudno.<br>\nGde‐to tut v olenjah i rogah<br>\nmy s toboj vrosli v tugie sud'by<br>\ntropami na vyzhzhennyh rukah.</p>\n<p>My plyvjom po Solncu,<br>\nnash korabl' chuet zapah gari.<br>\nTam, gde my prosnjomsja,<br>\ntam i rascvetjot gerbarij.</p>\n</section>"},"site":{"siteMetadata":{"languages":{"defaultLangKey":"en","langs":["en","ru"]}}}},"pathContext":{"slug":"/en/lyrics/gerbarij/"}}

